footer {/*Footer em si*/
    background-color: #0d6efd;/*Fundo*/
    color:white;/*Cor fonte*/
    font-size: 17px;/*Tamanho fonte*/
    font-family: 'Courier New', Courier, monospace;/*Fonte*/
    text-align: center;/*Alinhamento ao centro*/
    list-style-type: none;/*Decoração nula*/
    padding-top: 10px;/*Padding acima*/
}

#membros {/*Id membros*/
    font-size: 17px;/*Tamanho fonte*/
    font-family: 'Courier New', Courier, monospace;/*Fonte*/
    padding: 5px;/*Distância*/
}

.footerend {/*Footer final*/
    text-align: center;/*Alinhamento ao centro*/
    color: white;/*Cor fonte*/
    padding: 20px;/*Tamanho distância*/
    font-size: 17px;/*Tamanho fonte*/
    font-family: 'Courier New', Courier, monospace;/*Tamanho fonte*/
}